* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.item {
  display: flex;
  align-items: center;
}

img {
  display: block;
}

.icon {
  width: 40px;
  height: 40px;
}

.text {
  margin: 5px;
  padding-left: 5px;
}

.flag {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

.country {
  display: flex;
  align-items: center;
}

.span {
  font-weight: bold;
}
